<template>
    <div ref="nav" class="nav">
        <div class="nav-left">
            <div class="back-area" @click="back()">
                <ivButton type="text" icon="chevron-left" style="color:#2d8cf0;font-size:16px">返回</ivButton>
            </div>
            <span class="click-size size" @click="btn2click">{{btn2}}</span>
        </div>
        <div class="nav-middle">
            <span class="title">{{title}}</span>
        </div>
        <div class="nav-right">
            <slot>
                <div class="nav-right-content" @click="btn3clcik">
                    <span class="click-size">{{btn3}}</span>
                </div>
            </slot>
        </div>
    </div>
</template>

<script>
export default {
    name: 'CJCNav',
    props: ['title', 'btn2', 'btn3', 'custom'],
    components: {
        ivButton: () => import('iview/src/components/button')
    },
    methods: {
        /**
         * 触发btn2按钮自定义函数
         */
        btn2click() {
            this.$emit('btn2click')
        },
        /**
         * 触发btn3按钮自定义函数
         */
        btn3clcik() {
            this.$emit('btn3click')
        },
        /**
         * 触发返回按钮自定义函数
         */
        back() {
            this.$emit('customRouter')
        }
    }
}
</script>

<style lang="less">
@navColor: #ffffff;//nav背景颜色
@navTextColor: #000000;// nav字体颜色
@navHeight: 50px; // nav高度
.nav{
  width: 100%;
  height: 50px;
  background-color: @navColor;
  color: @navTextColor;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top:0;
  z-index: 9;
  box-shadow: 0 2px 3px #eaeaea;
  .click-size.size{
    font-size: 16px;
  }
  .title{
    font-weight: bold;
    font-size: 16px;
    letter-spacing: 2px;
  }
}
.nav-left{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 31%;
  span{
    margin-right: 5px;
  }
}
.nav-right{
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 31%;
  height: @navHeight;
  .nav-right-text{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 65px;
    height: @navHeight;
    &:active{
      background-color: @navColor/1.5;
    }
  }
  .nav-right-content{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    align-self: flex-end;
    width: 100%;
    height: @navHeight;
    margin-right: 10px;
    img{
      width: 25px;
    }
  }
}
.back-area{
  height: @navHeight;
  width: 69%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  >div{
    display: flex;
    justify-content: center;
    align-items: center;
    height: @navHeight;
    margin-right: 5px;
  }
  &:active{
    background-color: @navColor / 1.5;
  }
  .text{
    color: #0093dd;
    font-size: 16px;
  }
}
.back-img{
  overflow: hidden;
  img{
    height: 18px !important;
  }
}
.information-img{
  height: 18px !important;
  margin-right: 2px;
}

</style>



